$primary-color: #3a86ff;
$secondary-color: #4cc9f0;
$text-dark: #212529;
$text-light: #f8f9fa;
$bg-light: #f8f9fa;
$bg-dark: #515154;
$trash-color: #ff0000;
$border-radius: 0.75rem;
$box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);

.object {
    display: grid;
    grid-template-columns: 1fr;
    list-style: none;
    margin: .5rem 0;
    gap: 1.5rem;

    .object_structure {
        display: flex;
        flex-direction: column;
        gap: 1.25rem;
        padding: 1.25rem;
        border-radius: $border-radius;
        box-shadow: $box-shadow;
        background-color: $bg-light;
        border: 1px solid lighten($bg-dark, 60%);

        div {
            display: flex;
            flex-direction: column;

            label {
                font-weight: 700;
                font-size: 1rem;
                color: $text-dark;
                margin-bottom: 0.25rem;
                position: relative;

                &::after {
                    content: ':';
                }
            }

            span {
                color: $text-dark;
                background-color: lighten($bg-dark, 50%);
                padding: 0.75rem 1rem;
                border-radius: 0.5rem;
                font-family: 'Courier New', monospace;
                font-size: 1rem;
                line-height: 1.4;
                word-break: break-word;
                border: 1px solid lighten($bg-dark, 40%);
            }

        }

        .trashButton {
            border-radius: $border-radius;
            border: none;
            padding: .6rem;
            transition: 0.3s;

            &:hover {
                box-shadow: $box-shadow;
                color: red;
                cursor: pointer;
            }

        .trash {
            width: 1.5rem;
            height: auto;
        }}
    }
}